@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-bar__title,.section__title{display:block;font-weight:600}body{margin:50px 75px;font-size:16px;color:#363636;font-family:'Montserrat', sans-serif}.search-bar__area{box-sizing:border-box;width:600px;height:50px;margin-bottom:40px;padding-left:60px;padding-right:20px;cursor:pointer;background:url(/react_searchbar-and-articles/static/media/Vector.fa665393.svg);background-repeat:no-repeat;background-position:20px 15px}.search-bar__title{margin:0 0 10px}.search-bar__icon{width:20px;height:20px;margin-right:10px}.section__title{padding-bottom:5px;border-bottom:1px solid #eaeaea}

